
.outer-wrapper {
    display: flex;
    justify-content: center;
    width: var(--mid-container-width);
}

.nav-container {
    display: flex;
    color: var(--text-color);
}

.login-btn {
    margin-left: auto;
}

.nav-container a,
.nav-container span {
    padding: 15px 15px 15px 0;
    text-decoration: none;
}

.nav-container a:hover,
.nav-container span:hover {
    font-weight: bold;
}

.nav-container span {
    cursor: pointer;
    display: inline-block;
}

.active-link {
    font-weight: bold;
    color: var(--primary);
}

/* Mobile */

@media (max-width: 576px) {}

/* Large devices (desktops, 992px and up) */

@media (min-width: 992px) {}
.new-event-form,
.new-greeting-form {
    display: grid;
    grid-template-columns: 100px 1fr;
    grid-gap: 10px;
}

.guest-picker {
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 5px;
}

.guest-input {
    display: grid;
    grid-template-columns: 5rem 1fr;
    grid-gap: 5px;
}

.icon-button {
    width: 2rem;
}

input,
.form select{
    padding: 5px;
}


/* Mobile */

@media (max-width: 576px) {
    .new-event-form {
        grid-template-columns: 1fr;
    }
    .guest-input {
        display: grid;
        grid-template-columns: 1fr;
    }
}

/* Large devices (desktops, 992px and up) */

@media (min-width: 992px) {}
.greeting-tabs-container {
    display: flex;
    width: 100%;
    background-color: var(--primary-light);
}

.greeting-tab {
    color: white;
    padding: 10px;
    cursor: pointer;
}

.greeting-tab:hover {
    background-color: var(--primary);
}

.greeting-tab-pill {
    font-size: 0.8rem;
    border-radius: 50%;
    background-color: var(--secondary);
    width: 20px;
    height: 20px;
    display: inline-block;
    text-align: center;
    font-weight: bold;
    color: black;
}

.active-tab {
    background-color: var(--primary-dark);
}

.greeting-wrapper {
    border: 1px solid green;
    display: grid;
    grid-template-columns: -webkit-max-content 1fr;
    grid-template-columns: max-content 1fr;
    grid-gap: 10px;
}

.greeting-wrapper .buttons {
    display: flex;
}

.greeting-wrapper .buttons span {
    cursor: pointer;
    color: var(--secondary-dark);
    margin: 5px;
}

.greeting-wrapper .readonly * {
    padding: 0;
    margin: 0;
}

/* Mobile */

@media (max-width: 576px) {
    .greeting-wrapper {
        grid-template-columns: 1fr;
    }
}
.home {
    background-image: url(/static/media/main_bg-min.874a1043.jpg);
    background-size: cover;
    height: 200px;
}

.events {
    background-image: url(/static/media/events-bg.8826636b.jpg);
    background-size: cover;
    height: 200px;
}
.about {
    background-image: url(/static/media/about-bg.c37757eb.jpg);
    background-size: cover;
    height: 200px;
}
.party {
    background-image: url(/static/media/party-bg.c31c6e18.jpg);
    background-size: cover;
    height: 200px;
}

.hero {
    color: white;
    height: 200px;
    display: flex;
    align-items: center;
}

.hero .title {
    font-size: 4rem;
    font-weight: bold;
    text-shadow: 2px 2px 2px #333333;
}

.hero .description {
    font-size: 0.9rem;
}

.btn {
    padding: 10px 20px;
    margin: 10px 0;
    border: none;
    background-color: blueviolet;
    color: white;
    border-radius: 20px;
    box-shadow: 3px 6px 3px -3px #666666;
}

.loader-container {
    margin: 10px 0;
}

h3 {
    color: blueviolet;
}

.user-events {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5px;
}

.user-events>div {
    padding: 3px;
    display: grid;
    grid-gap: 15px;
    grid-template-columns: 200px 150px repeat(3, minmax(-webkit-min-content, -webkit-max-content));
    grid-template-columns: 200px 150px repeat(3, minmax(min-content, max-content));
}
:root {
    /* colors */
    --page-bg: #f9fafe;
    --text-color: #3a416f;
    --primary-dark: #6d1b7b;
    --primary: #9c27b0;
    --primary-light: #af52bf;
    --secondary-dark: #b28900;
    --secondary: #ffc400;
    --secondary-light: #ffcf33;
    /* dimensions */
    --min-container-width: 100%;
    --mid-container-width: 75%;
    --max-container-width: 75%;
    /* fonts */
    --body-font: 'Open Sans', sans-serif;
}

@font-face {
    font-family: 'Open Sans';
    src: local('OpenSans'), url(/static/media/OpenSans-Regular.d7d5d458.ttf) format('truetype');
}

@font-face {
    font-family: 'Open Sans';
    src: local('OpenSans'), url(/static/media/OpenSans-Bold.f5331cb6.ttf) format('truetype');
    font-weight: bold;
}

body,
html {
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    font-family: var(--body-font);
    background-color: #f9fafe;
    background-color: var(--page-bg);
}

button {
    font-family: 'Open Sans', sans-serif;
    font-family: var(--body-font);
}

.outer-wrapper {
    width: 100%;
}

.container {
    width: 75%;
    width: var(--mid-container-width);
}
/* Mobile */

@media (max-width: 576px) {
    .container {
        width: 100%;
        width: var(--min-container-width);
    }
}

/* Large devices (desktops, 992px and up) */

@media (min-width: 992px) {
    .container {
        width: 75%;
        width: var(--max-container-width);
    }
}
